import React, { useState, useEffect } from 'react';
//import './styles/AllPlayersView.css';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom'

const StudentData= {Name: 'Timmy Turner',GPA: '2.3', email: 'cosmowanda@aol.com',campus: 'Queens',image: 'data:image/png;base64,'}


const EditForm = ({ student, campuses, setValue, onSubmit, formRef }) => {
  //const { name, address, description, imageUrl } = campus
  return (
  <form onSubmit={e => {e.preventDefault(); console.log('Submitted'); onSubmit()}} ref={formRef}>
    <label for='Name'>First Name:</label>
    <input type= 'text' id='Name' name='Name' value={student?.firstname} onChange={e => setValue({ ...student, firstname: e.target.value })} required/><br></br>
    <label for='lName'>Last Name:</label>
    <input type= 'text' id='lName' name='lName' value={student?.lastname} onChange={e => setValue({ ...student, lastname: e.target.value })} required/><br></br>
    <label for='Email'>Email:</label>
    <input type= 'email' id='Email' name='Email' value={student?.email} onChange={e => setValue({ ...student, email: e.target.value })} required/><br></br>
    <label for='GPA'>GPA:</label>
    <input type= 'text' id='GPA' name='GPA' value={student?.gpa} onChange={e => setValue({ ...student, gpa: e.target.value })} /><br></br>
    <label for='campus'>Campus:</label>
    <select value={student.campusId} onChange={e => setValue({ ...student, campusId: e.target.value === "" ? null : e.target.value })}>
      <option value="">Not Assigned</option>
      { campuses.map(({ id, name }) => <option value={id}>{`${id}: ${name}`}</option>) }
    </select>
    <br></br>
    <label for='imageUrl'>Profile Image URL:</label>
    <input type= 'url' id='imageUrl' name='imageUrl' value={student?.imageUrl} onChange={e => setValue({ ...student, imageUrl: e.target.value })} /><br></br>
  </form>)
}

const SingleStudent = ({ isEditing, formRef, student, campuses, onSubmit }) => {
  const [studentForm, setStudent] = useState(student)

  useEffect(() => {
    setStudent(student)
  }, [student, isEditing])

 //console.log(student)
  return (
    <div className="data">
      { isEditing ?
        <EditForm formRef={formRef} student={studentForm} campuses={campuses} setValue={setStudent} onSubmit={() => onSubmit(studentForm)} />
        : (
      <p>
      First Name: {student?.firstname}<br/>Last Name: {student?.lastname}<br/>Email: {student?.email}<br/>Campus: {student?.campusId ?? false ? <Link to={`/campuses/${student.campusId}`}>{student.campusId}</Link> : 'Not Assigned' }<br/>GPA: {student?.gpa}<br/><img src={student?.imageUrl} alt='' className='stuImage'/>
      </p>
        )
    }
    </div>
  );
};



SingleStudent.propTypes = {
  StudentData: PropTypes.array.isRequired
};

export default SingleStudent;
